}

summary {
  display: list-item;
}

a {
  text-decoration: none;
  transition-property: fill, color, background-color, transform, border-color, letter-spacing;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

button {
  transition-property: color, background-color, box-shadow, fill, transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes translatey {
  0% {
    filter: blur(18px);
    opacity: 0;
    transform: translateY(100%);
  }

  100% {
    filter: blur();
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes focus-in {
  0% {
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    filter: blur();
    opacity: 1;
  }
}

