// fonts
$primary-font-family: Montserrat, sans-serif;
$secondary-font-family: Seymour One, sans-serif;

// colors
$colors: (
  'primary-text-color': #fcfcfc,
  'secondary-text-color': #131313,
  'accent': #f8d24e,
  'time-text-color': #ababab,
  'address-text-color': #d6936d,
  'ice-cream-bg-color': #aec6a5,
  'ice-coffee-bg-color': #efd478,
);
//    -    getcolor(  ( ,    COLORS.    ))
@function getcolor($key) {
  @return map-get($colors, $key);
}

// breakpoints

$mobile: 480px;
$pre-tablet: 767px;
$tablet: 768px;
$pre-desktop: 1199px;
$desktop: 1200px;

// animation

$time-speed: 250ms;
$time-function: cubic-bezier(0.4, 0, 0.2, 1);
